<template>
  <div>
    <content-title
      title="收信设置"
      tips="收信时间间隔设置、源邮件设置(仅管理员可进行操作)">
    </content-title>
    <content-c>
      <div class="row">
        <p class="com-red-dot">收信时间间隔</p>
        <p><input class="time" type="number" :placeholder="mailDetail.interval" v-model="interval" min="0">小时</p>
        <p>
          <button @click="saveIntervalSet">保存修改</button>
        </p>
      </div>
      <div class="row">
        <p>源邮件设置</p>
        <p class="com-red-dot">源邮件: <input class="email" :placeholder="mailDetail.username" v-model="origin.username"
                                           type="email"></p>
        <p class="com-red-dot">源邮件密码: <input class="password" :placeholder="mailDetail.password"
                                             v-model="origin.password" type="text"></p>
        <p>SMTP/S链接参数: <input class="smtp" :placeholder="mailDetail.protocol" v-model="origin.receiveProtocol"
                              type="text"></p>
        <p>
          <button @click="saveOriginSet">保存修改</button>
        </p>
      </div>
    </content-c>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import { mapState, mapActions } from 'vuex'

  export default {
    name: '',
    props: {},
    data () {
      return {
        interval: '',
        origin: {
          username: '',
          password: '',
          protocol: ''
        }
      }
    },
    computed: {
      ...mapState('email', [
        'mailDetail',
        'receivedMsgSetStatus',
        'originEmailSetStatus',
      ])
    },
    watch: {
      receivedMsgSetStatus (d) {
        if (d) {
          this.saveSuccess()
        }
      },
      originEmailSetStatus (d) {
        if (d) {
          this.saveSuccess()
        }
      }
    },
    methods: {
      ...mapActions('email', [
        'ac_EmailSetShow',
        'ac_originEmailSet',
        'ac_receivedMsgIntervalSet',
      ]),
      saveIntervalSet () {
        if (this.interval) {
          this.ac_receivedMsgIntervalSet({
            interval: this.interval
          })
        } else {
          this.$message({
            type: 'warning',
            message: '请输入收信周期'
          })
        }
      },
      saveOriginSet () {
        if (this.origin.username && this.origin.password) {
          this.ac_originEmailSet(Object.assign({}, this.origin, {interval: this.mailDetail.interval}))
        } else {
          this.$message({
            type: 'warning',
            message: '未修改或源邮件或有邮件密码不能为空！'
          })
        }
      },
      saveSuccess () {
        this.ac_EmailSetShow()
        this.$message({
          type: 'success',
          message: '保存成功'
        })
        this.interval = ''
        this.origin = {
          username: '',
          password: '',
          protocol: ''
        }
      }
    },
    components: {
      contentTitle,
      contentC,
    },
    beforeCreate () {},
    created () {
      this.ac_EmailSetShow()
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .add-btn {
    padding: 6px 10px;
    background-color: #45C5C0;
    color: #ffffff;
    width: 110px;
    border-radius: 20px;
    text-align: center;
    float: right;
    margin-right: 10px;
    margin-top: -45px;
    cursor: pointer;
  }

  .row {
    font-size: 12px;
    border: 1px solid #DFDFDF;
    background-color: #F4F4F4;
    padding: 30px 20px;
    border-radius: 5px;
    margin-bottom: 20px;
    color: #767676;
    input {
      color: #767676;
      border-radius: 5px;
      border: 1px solid #DBDBDB;
      font-size: 14px;
      margin-top: 20px;
      height: 30px;
      padding-left: 10px;
    }
    button {
      color: #fff;
      background-color: #FDC61E;
      border-radius: 5px;
      border: 1px solid #DBDBDB;
      padding: 3px 20px;
      margin-top: 30px;
    }
    .time {
      width: 90px;
      margin-right: 5px;
      text-align: center;
    }
    .email {
      width: 580px;
    }
    .password {
      width: 555px;
    }
    .smtp {
      width: 528px;
    }
  }
</style>



